@import './mix.less';

html {
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;
    overflow-y: auto;

    &.overfl {
        overflow-y: hidden;
    }
}

input {
    &:focus {
        box-shadow: none !important;
        border: 1px solid #ced4da !important;
    }
}

body {
    width: 100%;
    max-width: 100%;
    overflow-x: hidden;

    &.overfl {
        overflow-y: hidden;
    }
}

body {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    // overflow-x: hidden;

    h3 {
        color: #ffffff;
        font-size: 14px;
        font-weight: 600;
    }

    // color: rgb(255, 0, 0) !important;
    h1,
    h2,
    h3,
    h4,
    h5 {
        margin: 0;
        // color: rgb(255, 0, 0) !important;
    }

    p {
        margin: 0;
        // color: rgb(255, 0, 0) !important;
    }

    a {
        // color: #000;
        // color: rgb(255, 0, 0) !important;
        text-decoration: none;

        &:hover {
            text-decoration: none;
        }
    }

    ul {
        padding: 0;
        margin: 0;

        li {
            margin: 0;
            padding: 0;
            list-style: none;
        }
    }

    hr {
        margin: 0;
        padding: 0;
    }

    .register-login {
        h2 {
            font-size: 26px;
            font-weight: 600;
            color: #253354;
        }
    }

    .mobile-menu {
        display: none;
    }
}


.modal {
    padding-top: 163px;
    color: #253354;
    overflow-x: hidden;

    .modal-dialog {
        max-width: 728px;
        transform: translateX(7px) !important;

        .modal-content {
            width: 728px;

            .modal-header {
                padding-top: 19px;
                border-bottom: 0;
                padding-left: 0;
                padding-left: 29px;

                h5 {
                    font-size: 26px;
                }

                .close {
                    display: block;
                    background: transparent;
                    transform: translateY(-12px);

                    span {
                        background: transparent;
                        display: block;
                        transform: translateX(2px);

                        &:first-child {
                            display: none;
                        }
                    }
                }
            }

            .modal-body {
                padding-left: 0;
                padding-top: 0;
                border-bottom: 0;
                padding-left: 29px;

                form {
                    display: flex;

                    .form-group {
                        margin-bottom: 0;

                        &:nth-child(1) {
                            width: 228px;
                            margin-right: 31px;
                        }

                        &:nth-child(2) {
                            width: 149px;
                            margin-right: 30px;
                        }

                        &:nth-child(3) {
                            width: 228px;
                        }

                        label {
                            margin-bottom: 3px;
                            font-size: 14px;
                        }

                        select {
                            height: 39px;
                            font-size: 14px;
                            padding-bottom: 1px;
                            padding-left: 9px;
                            margin-bottom: 15px;
                            position: relative;

                            &:before {
                                content: ">";
                                display: block;
                                position: absolute;
                                width: 10px;
                                height: 10px;
                                top: 0;
                            }
                        }
                    }
                }

                .methods {
                    &.shipping-methods {
                        .img-group {
                            display: flex;
                            align-items: center;
                            transform: translateX(-2px);

                            a {
                                margin-right: 11px;

                                &:nth-child(2) {
                                    margin-right: 16px;
                                    transform: translateY(-4px);
                                }

                                &:nth-child(3) {
                                    margin-right: 0;
                                    transform: translateY(-5px);
                                }
                            }
                        }
                    }

                    &.payment-methods {
                        .img-group {
                            display: flex;
                            align-items: center;
                            transform: translateX(-2px);

                            a {
                                &:nth-child(1) {
                                    margin-right: 13px;
                                    transform: translateY(-4px);
                                }

                                &:nth-child(2) {
                                    margin-right: 15px;
                                    transform: translateY(-3px);
                                }

                                &:nth-child(3) {
                                    margin-right: 0;
                                    transform: translateY(-4px);
                                }
                            }
                        }
                    }

                    span {
                        font-size: 13px;
                        color: #525f7e;
                        display: block;
                        margin-bottom: 10px;
                    }
                }
            }

            .modal-footer {
                border-top: 0;
                background: #F2F4F6;
                display: flex;
                justify-content: center;

                .btn {
                    text-transform: uppercase;
                    font-weight: 600;
                    width: 204px;
                    margin-top: 5px;
                    height: 42px;
                    padding-top: 2px;
                    padding-left: 15px;
                    &:hover {
                        background: #6AA2FF;
                        border: 1px solid #6AA2FF;
                    }
                    &:focus {
                        background: #145BCF;
                        box-shadow: none;
                        border: 1px solid #145BCF;
                    }
                }
            }
        }
    }

}

